<template>
  <v-app>
    <v-app-bar class="secondaryBack">
      <v-container class="d-flex align-center">
        <img src="https://alpha.emdx.io/img/logo.png" alt="">
        <v-spacer></v-spacer>
        <metaMaskConnect :activateMetaMask="activateMetaMask" />
      </v-container>
    </v-app-bar>
    <div class="background">
      <casino-component :activateMetaMask="activateMetaMask" />
    </div>
<!--     <hello-metamask /> -->
  </v-app>
</template>
<script>
import { mapState } from "vuex";
import CasinoComponent from "@/components/casino-component";
import metaMaskConnect from "@/components/metaMaskConnect";
export default {
  name: "casino-dapp",
  beforeCreate() {
    console.log("registerWeb3 Action dispatched from casino-dapp.vue");
    this.$store.dispatch("registerWeb3");
  },
  components: {
    "casino-component": CasinoComponent,
    "metaMaskConnect": metaMaskConnect
  },
  methods: {
    activateMetaMask() {
      window.ethereum.enable();
    }
  },
  computed: mapState({
    coinbase: (state) => state.web3.coinbase,
    ethBalance: state => {
      if (state.web3.web3Instance !== null) return state.web3.web3Instance().fromWei(state.web3.balance, 'ether')
    },
    coinbase: state => state.web3.coinbase,
  }),
};
</script>
<style scoped>

</style>



